.container {
  width: 320px;
  height: auto;
  margin: auto;
  padding: 30px 10px;
  border-radius: 12px;
  background-color: #FAD6A5;
  /* background-image: linear-gradient(180deg, #130754 0%, #3b2f80 100%); */
}

.top--bar {
  display: flex;
  justify-content: space-between;
}

.top--bar input {
  display: flex;
  width: 70%;
  height: 40px;
  background-color: #ebfffc;
  border: none;
  outline: none;
  border-radius: 10px;
  padding-left: 20px;
  color: #626262;
  font-size: 14px;
  font-weight: 400;
}

.search--icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 15%;
  height: 40px;
  background: #ebfffc;
  border-radius: 20px;
  cursor: pointer;
}

.weather--image {
  margin-top: 25px;
  display: flex;
  justify-content: center;
}

.weather--temp{
  display: flex;
  justify-content: center;
  font-size: 80px;
  color: black;
  font-weight: 400;
}

.weather--location {
  display: flex;
  justify-content: center;
  font-size: 60px;
  color: black;
  font-weight: 400;
  font-style: italic;
}

.data--container {
  margin-top: 50px;
  color: black;
  display: flex;
  justify-content: space-between;
}

.element {
  margin: auto;
  display: flex;
  align-items: flex-start;
  gap: 12px;
}

.element img {
  width: 30px;
}

.data {
  font-size: 25px;
  font-weight: 400;
}

.text {
  font-size: 15px;
  font-weight: 400;
}

.icon {
  margin-top: 10px;
}